@import "components/header";
@import "components/asside";
@import "components/footer";

body{
  .content{
    margin: 25px 0 35px;
    min-height: 1800px;
    .container{
      .row{
        .col-lg-10{
          h5{
            margin: 35px 0 0 15px;
            font: 1.25em/1.375em "Arial";
            font-weight: bold;
            text-transform: uppercase;
          }
        }
        .col-lg-12{
          display: flex;
          flex-wrap: wrap;
          .cart{
            margin: 15px 10px 0;
            width: 20%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            position: relative;
            .delete{
              display: block;
              position: absolute;
              top: 5px;
              right: 5px;
              i{
                color: #e95959;
              }
            }
            a{
              height: 325px;
              display: block;
              text-decoration: none;
              img{
                width: 100%;
                padding: 15px 20px;
              }
              span{
                display: block;
                padding: 15px 20px;
                font: .875em/1em "Arial";
                font-weight: bold;
                color: #0081ef;
              }
            }
            ul{
              display: flex;
              li{
                a{
                  width: 100%;
                  height: 40px!important;
                  display: inline-block;
                  font: .875em/40px "Arial";
                  font-weight: bold;
                  color: #777;
                  text-align: center;
                  background-color: #eee;
                  border-top: 1px solid #dfdfdf;
                }
                &:nth-child(1){
                  width: 60%;
                }
                &:nth-child(2){
                  width: 40%;
                }
                .heart{
                  border-left: 1px solid #dfdfdf;
                }
              }
            }
            .compare__data{
              margin: 20px 0 0;
              span{
                padding: 0 0 0 40px;
                display: block;
                font: .938em/40px "Arial";
                color: #525252;
                border-bottom: 1px solid #cacaca;
              }
            }
          }
          .compare__data{
            margin: 15px 0 0;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            ul{
              display: flex;
              li{
                margin: 0 10px;
                width: 20%;
                height: 40px;
                p{
                  padding: 0 0 0 15px;
                  font: .938em/40px "Arial";
                  font-weight: bold;
                  color: #525252;
                  border-bottom: 1px solid #cacaca;
                }
                span{
                  padding: 0 0 0 40px;
                  display: block;
                  font: .938em/40px "Arial";
                  color: #525252;
                  border-bottom: 1px solid #cacaca;
                }

              }
            }
          }
        }
        .col-lg-12{
          .attention{
            margin: 40px 0 25px;
            font: .938em/1.063em "Arial";
            color: #525252;
            span{
              font-weight: bold;
            }
          }
        }
      }
    }
  }
}